/* eslint-disable react/no-unescaped-entities */
import React, { useState, useRef } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars } from "@fortawesome/free-solid-svg-icons";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import Home from "./Home";
import AboutMe from "./AboutMe";
import MyExperience from "./MyExperience";
import Portfolio from "./Portfolio";
import Resume from "./Resume";
import Footer from "./Footer";
import "./style.css";
const scrollToAboutMe = (ref) => window.scrollTo(0, ref.current.offsetTop);

const Index = () => {
  const [clicked, setClicked] = useState(false);
  const myAboutMe = useRef(null);
  const aboutMeGo = () => scrollToAboutMe(myAboutMe);

  return (
    <div className="content">
      <FontAwesomeIcon
        onClick={() => setClicked(!clicked)}
        icon={faBars}
        className="barsIcon"
        style={
          clicked
            ? { borderRadius: "0px 20px 20px 0px", display: "none" }
            : { borderRadius: "20px" }
        }
      />
      <FontAwesomeIcon
        icon={faTimes}
        onClick={() => setClicked(!clicked)}
        className="barsIcon"
        style={
          clicked ? { borderRadius: "0px 20px 20px 0px" } : { display: "none" }
        }
      />
      <div className={clicked ? "menu clicked" : "menu"}>
        <a href="javascript:void(0);" onClick={aboutMeGo}>
          About Me
        </a>
        <a href="">Service</a>
        <a href="">Portfolio</a>
        <a href="">Resume</a>
      </div>
      <Home />
      <AboutMe myAboutMe={aboutMeGo} />
      <MyExperience />
      <Portfolio />
      <Resume />
      <Footer />
    </div>
  );
};

export default Index;
